import React from "react";
import { useState } from "react";
import { useEffect } from "react";
import { useSelector } from "react-redux";


export const SearchResult = () => {
    const { cards, search } = useSelector((s) => s.products)
    const [item, setItem] = useState('')
    const [found, setFound] = useState('')

    const CountProduct = () => {
        if (cards.length === 1) {
            return ""
        } else if (cards.length > 1 && cards.length < 5) {
            return ""
        } else if (cards.length > 1 || cards.length === 0) {
            return ""
        } 
        
    };
    
    const FoundProduct = () => {
        if (cards.length === 1) {
            return ""
        } else {
            return ""      
        }
    };

    useEffect(()=> {
        setItem(CountProduct())
        setFound(FoundProduct())
    },[])


    return <p className="main_result">   <b>{search}</b> {found} {cards.length} {item} </p>
}